let that;
import Bmob from "hydrogen-js-sdk";

export default {
	components: {
	},
	data() {
		return {
			detail:''
		};
	},
	onLoad() {
		that = this;
		const query = Bmob.Query("operations");
		query.order("-order")
		query.find().then(res => {
			that.detail = res
		});
	},
	onShow() {
		
	},
	methods: {
		
		gotoDet(content){
			uni.setStorageSync("operationDet",content)
			uni.navigateTo({
				url:"detail/detail"
			})
		}

	}
}
